import {
  CircularProgress,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
} from '@material-ui/core';
import React, { ReactNode } from 'react';

import { IStats } from '../../data/stats/stats';
import StyledLink from '../StyledLink';

interface IStatsRowProps {
  label: string;
  value?: any;
  link?: string;
  format?: (value: any) => ReactNode;
}

const StatsRow = ({
  label,
  link,
  value,
  format = (value) => value,
}: IStatsRowProps) => (
  <TableRow>
    <TableCell sx={{ width: '50%' }}>{label}</TableCell>
    <TableCell sx={{ width: '50%' }}>
      {link ? (
        <StyledLink to={link} label={format(value) || value}></StyledLink>
      ) : (
        format(value) || <CircularProgress size={16} />
      )}
    </TableCell>
  </TableRow>
);

interface IStatsTableProps {
  stats: IStats[];
}

const StatsTable = ({ stats }: IStatsTableProps) => (
  <TableContainer>
    <Table>
      <TableBody>
        {stats.map((stat: IStats) => (
          <StatsRow
            format={stat.format}
            key={stat.label}
            label={stat.label}
            link={stat.link}
            value={stat.value}
          />
        ))}
      </TableBody>
    </Table>
  </TableContainer>
);

export default StatsTable;
