import PageSectionLayout, {
  IPageSectionLayoutProps,
} from '../PageSectionLayout';

import React from 'react';
import StyledTypography from '../StyledTypography';

interface IResumeDetailsLayoutProps {}

const ResumeDetailsLayout = ({
  title,
  ...restProps
}: IResumeDetailsLayoutProps & IPageSectionLayoutProps) => {
  return (
    <PageSectionLayout
      title={
        <>
          {typeof title === 'string' && (
            <StyledTypography
              bold={true}
              capitalized={true}
              gutterBottom={true}
              variant="body1"
              textAlign="center"
              spaced={true}>
              {title}
            </StyledTypography>
          )}
          {typeof title !== 'string' && <>{title}</>}
        </>
      }
      {...restProps}
    />
  );
};

export default ResumeDetailsLayout;
