import { Box, Stack } from '@material-ui/core';
import React, { ReactNode } from 'react';

import PageContentLayout from '../layout/PageContentLayout';
import PageLayout from '../layout/PageLayout';
import PersonalStats from '../components/Stats/PersonalStats';
import SiteStats from '../components/Stats/SiteStats';

const sections: ReactNode[] = [
  <PersonalStats key="personal-stats" />,
  <SiteStats key="site-stats" />,
];

interface IStatsProps {}

const Stats = ({}: IStatsProps) => {
  const pageName = 'Stats';
  return (
    <>
      <PageLayout title={pageName} description={pageName}>
        <PageContentLayout header={pageName}>
          <Stack spacing={2}>
            {sections.map((section: ReactNode, index: number) => (
              <Box key={`stats-section-${index}`} display="flex" pb={2}>
                {section}
              </Box>
            ))}
          </Stack>
        </PageContentLayout>
      </PageLayout>
    </>
  );
};

export default Stats;
