import React, { ReactNode } from 'react';

import { Stack } from '@material-ui/core';
import StyledTypography from './StyledTypography';

export interface IPageSectionLayoutProps {
  id?: string;
  title: ReactNode;
  children: ReactNode;
}

const PageSectionLayout = ({
  title,
  children,
  id,
}: IPageSectionLayoutProps) => (
  <>
    <Stack
      spacing={2}
      id={id}
      sx={{
        width: '100%',
      }}>
      {typeof title === 'string' && (
        <StyledTypography
          bold={true}
          capitalized={true}
          gutterBottom={true}
          variant="body1"
          spaced={true}>
          {title}
        </StyledTypography>
      )}
      {typeof title !== 'string' && <>{title}</>}
      {children}
    </Stack>
  </>
);

export default PageSectionLayout;
