import jobs, { IJob } from '../../data/resume/job';

import Job from './Experience/Job';
import React from 'react';
import ResumeDetailsLayout from './ResumeDetailsLayout';

const Experience = () => {
  return (
    <ResumeDetailsLayout title="Experience">
      {jobs.map((job: IJob, index: number) => (
        <Job key={`${job.company}${index}`} {...job} />
      ))}
    </ResumeDetailsLayout>
  );
};

export default Experience;
