import { Box, Typography } from '@material-ui/core';

import { IDegree } from '../../../data/resume/degree';
import React from 'react';
import StyledLink from '../../StyledLink';
import StyledTypography from '../../StyledTypography';

const Degree = ({ degree, school, link, toYear }: IDegree) => {
  return (
    <>
      <Box>
        <Typography gutterBottom={false}>{degree}</Typography>
        <Box style={{ display: 'inline' }}>
          <StyledLink to={link} label={school} target="_blank">
            <StyledTypography
              fontVariant="subtitle"
              variant="overline"
              aria-label="subtitle"
              capitalized={true}
              hoverEffect={true}>
              {school}
            </StyledTypography>
          </StyledLink>
          <StyledTypography
            fontVariant="subtitle"
            variant="overline"
            aria-label="subtitle"
            capitalized={true}>
            , {toYear}
          </StyledTypography>
        </Box>
      </Box>
    </>
  );
};

export default Degree;
