import { Box, Button, ButtonProps, Theme, styled } from '@material-ui/core';
import { ICategory, ISkill } from '../../../data/resume/skills';

import React from 'react';
import StyledButton from '../../StyledButton';
import StyledTypography from '../../StyledTypography';
import useBreakpoints from '../../../hooks/useBreakpoints';

const StyledButtonX = styled(StyledButton)<ButtonProps>(({ theme }) => {
  return {
    padding: `${theme.spacing(1)} ${theme.spacing(1)}`,
    borderColor: 'transparent',
  };
});

interface ICategoryButtonProps {
  label: string;
  handleClick: (label: string) => void;
  isActive: boolean;
}

const Category = ({ label, handleClick, isActive }: ICategoryButtonProps) => {
  const { sm } = useBreakpoints();
  const onClick = () => {
    handleClick(label);
  };
  return (
    <>
      <Box
        sx={{
          m: sm ? 0 : 1 / 2,
        }}>
        <StyledButtonX
          onClick={onClick}
          sx={{
            ...(isActive
              ? {
                  color: (theme: Theme) => theme.palette.primary.main,
                  borderColor: (theme: Theme) => theme.palette.primary.main,
                }
              : {}),
          }}>
          <StyledTypography
            fontVariant="titleLight"
            capitalized={true}
            bold={true}>
            {label}
          </StyledTypography>
        </StyledButtonX>
      </Box>
    </>
  );
};

export default Category;
