import { Box, Stack, Typography } from '@material-ui/core';
import React, { ReactNode } from 'react';

import PageContentLayout from '../layout/PageContentLayout';
import PageLayout from '../layout/PageLayout';
import WorkProjects from '../components/Project/WorkProjects';

const sections: ReactNode[] = [<WorkProjects key="work-projects" />];

interface IProjectsProps {}

const Projects = ({}: IProjectsProps) => {
  const pageName = 'Projects';
  return (
    <>
      <PageLayout title={pageName} description={pageName}>
        <PageContentLayout
          header={pageName}
          subHeader="A selection of projects that I'm not too ashamed of">
          <Stack spacing={2}>
            {sections.map((section: ReactNode, index: number) => (
              <Box key={`projects-section-${index}`} display="flex" pb={2}>
                {section}
              </Box>
            ))}
          </Stack>
          <Typography typography="body2" color="text.disabled">
            (More projects are being added)
          </Typography>
        </PageContentLayout>
      </PageLayout>
    </>
  );
};

export default Projects;
