import { Box, Stack } from '@material-ui/core';

import { ICertificate } from '../../../data/resume/certification';
import React from 'react';
import StyledLink from '../../StyledLink';
import StyledTypography from '../../StyledTypography';
import VerifiedSharpIcon from '@material-ui/icons/VerifiedSharp';

const Certificate = ({
  name,
  issuer,
  issuedOn,
  verificationLink,
}: ICertificate) => {
  return (
    <>
      <Box>
        {verificationLink && (
          <Stack direction="row" alignItems="center" spacing={1}>
            <StyledTypography fontVariant="titleLight" capitalized={true}>
              {name}
            </StyledTypography>
            <StyledLink to={verificationLink} label={name} target="_blank">
              <VerifiedSharpIcon
                sx={{
                  color: 'green',
                }}
              />
            </StyledLink>
          </Stack>
        )}
        {!verificationLink && (
          <StyledTypography fontVariant="titleLight" capitalized={true}>
            {name}
          </StyledTypography>
        )}
        <StyledTypography
          fontVariant="subtitle"
          variant="overline"
          aria-label="subtitle"
          capitalized={true}>
          {`${issuer} - ${issuedOn}`.toUpperCase()}
        </StyledTypography>
      </Box>
    </>
  );
};

export default Certificate;
