import { Box, LinkProps, Typography } from '@material-ui/core';

import React from 'react';
import ReactMarkdown from 'markdown-to-jsx';
import StyledLink from './StyledLink';
import StyledTypography from './StyledTypography';

function MarkdownListContainer(props: any) {
  return <Box component="ul" sx={{ marginBottom: 4 }} {...props} />;
}

function MarkdownListItem(props: any) {
  return (
    <Box component="li" sx={{ mt: 1 }}>
      <Typography variant="body1" {...props}></Typography>
    </Box>
  );
}

function MarkdownLink(props: LinkProps) {
  return (
    <StyledLink
      to={props.href ?? '/'}
      label={props.title ?? React.Children.toArray(props.children)?.join(', ')}
      target="_blank"
    />
  );
}

function h1Styled(props: any) {
  return (
    <>
      <StyledTypography
        key={props.id}
        fontVariant="titleLight"
        bold={true}
        capitalized={true}
        gutterBottom={true}
        sx={{
          pb: 2,
        }}>
        {React.Children.toArray(props.children)?.join(', ')}
      </StyledTypography>
    </>
  );
}

function h2Styled(props: any) {
  return <Typography variant="h2">{props.children}</Typography>;
}

function h3Styled(props: any) {
  return <Typography variant="h3">{props.children}</Typography>;
}

function h4Styled(props: any) {
  return <Typography variant="h4">{props.children}</Typography>;
}

function h5Styled(props: any) {
  return <Typography variant="h5">{props.children}</Typography>;
}

function h6Styled(props: any) {
  return <Typography variant="h6">{props.children}</Typography>;
}

function pStyled(props: any) {
  return (
    <Typography
      paragraph={true}
      gutterBottom={true}
      sx={{
        pb: 2,
      }}>
      {props.children}
    </Typography>
  );
}

const options = {
  overrides: {
    h1: {
      component: h1Styled,
    },
    h2: {
      component: h2Styled,
    },
    h3: {
      component: h3Styled,
    },
    h4: {
      component: h4Styled,
    },
    h5: {
      component: h5Styled,
    },
    h6: {
      component: h6Styled,
    },
    p: {
      component: pStyled,
    },
    a: { component: MarkdownLink },
    ul: { component: MarkdownListContainer },
    li: {
      component: MarkdownListItem,
    },
  },
};

export default function Markdown(props: any) {
  return <ReactMarkdown options={options} {...props} />;
}
