import { Box, Link, Typography } from '@material-ui/core';

import { IDegree } from '../../../data/resume/degree';
import { IJob } from '../../../data/resume/job';
import React from 'react';
import StyledLink from '../../StyledLink';
import StyledTypography from '../../StyledTypography';

const Job = ({ company, position, link, dateRange, points }: IJob) => {
  return (
    <>
      <Box>
        <StyledLink
          to={link}
          label={company}
          target="_blank"
          bottomBorder={false}>
          <StyledTypography fontVariant="titleLight" capitalized={true}>
            {`${company} - ${position}`.toUpperCase()}
          </StyledTypography>
        </StyledLink>
        <StyledTypography
          fontVariant="subtitle"
          variant="overline"
          aria-label="subtitle"
          capitalized={true}>
          {dateRange}
        </StyledTypography>
        <ul
          style={{
            paddingInlineStart: 16,
          }}>
          {points.map((point, index) => (
            <li key={`${company}-point-${index}`}>
              <Typography>{point}</Typography>
            </li>
          ))}
        </ul>
      </Box>
    </>
  );
};

export default Job;
