import { ImageList, ImageListItem, Stack, Typography } from '@material-ui/core';
import { categories, skills } from '../../data/resume/skills';

import Category from './Skills/Category';
import React from 'react';
import ResumeDetailsLayout from './ResumeDetailsLayout';
import SkillBadge from './Skills/SkillBadge';
import useBreakpoints from '../../hooks/useBreakpoints';
import { useState } from 'react';

interface ICategories {
  All: boolean;
  [key: string]: boolean;
}

interface ISkillsState {
  categories: ICategories;
  selectedSkill: string;
}

const Skills = () => {
  const { sm } = useBreakpoints();
  const [state, setState] = useState<ISkillsState>({
    categories: categories
      .map((cat) => cat.name)
      .reduce<ICategories>(
        (obj, key) => ({
          ...obj,
          [key]: false,
        }),
        { All: true },
      ),
    selectedSkill: 'All',
  });

  const getSkillBadges = () => {
    // search for true active categories
    const actCat = Object.keys(state.categories).reduce(
      (cat, key) => (state.categories[key] ? key : cat),
      'All',
    );

    return skills
      .sort((a, b) => {
        let ret = 0;
        if (a.competency > b.competency) ret = -1;
        else if (a.competency < b.competency) ret = 1;
        else if (a.categories[0] > b.categories[0]) ret = -1;
        else if (a.categories[0] < b.categories[0]) ret = 1;
        else if (a.title > b.title) ret = 1;
        else if (a.title < b.title) ret = -1;
        return ret;
      })
      .filter((skill) => actCat === 'All' || skill.categories.includes(actCat))
      .map((skill) => (
        <ImageListItem key={skill.title}>
          <SkillBadge categories={categories} data={skill} />
        </ImageListItem>
      ));
  };

  const getCategories = () => {
    return Object.keys(state.categories).map((key) => (
      <Category
        label={key}
        key={key}
        isActive={state.selectedSkill === key}
        handleClick={handleChildClick}
      />
    ));
  };

  const handleChildClick = (label: string) => {
    setState((prevState: ISkillsState) => {
      // Toggle button that was clicked. Turn all other buttons off.
      const buttons = Object.keys(prevState.categories).reduce<ICategories>(
        (obj, key) => ({
          ...obj,
          [key]: label === key && !prevState.categories[key],
        }),
        { All: false },
      );
      // Turn on 'All' button if other buttons are off
      buttons.All = !Object.keys(prevState.categories).some(
        (key) => buttons[key],
      );
      return {
        ...prevState,
        ...{ categories: buttons, selectedSkill: buttons.All ? 'All' : label },
      };
    });
  };

  return (
    <ResumeDetailsLayout title="Skills">
      <Typography color="text.disabled" variant="body2" textAlign="center">
        Note: I think these sections are silly, but everyone seems to have one.
        Here is a *mostly* honest overview of my skills.
      </Typography>
      <Stack
        direction="row"
        justifyContent="center"
        sx={{
          flexWrap: 'wrap',
        }}>
        {getCategories()}
      </Stack>
      <ImageList cols={sm ? 2 : 3}>{getSkillBadges()}</ImageList>
      <Typography typography="body2" color="text.disabled" textAlign="center">
        (Skills are being refined)
      </Typography>
    </ResumeDetailsLayout>
  );
};

export default Skills;
