import React from 'react';
import StatsDetailsLayout from './StatsDetailsLayout';
import StatsTable from './StatsTable';
import { personalStats } from '../../data/stats/stats';

const PersonalStats = () => {
  return (
    <StatsDetailsLayout title="Some stats about me">
      <StatsTable stats={personalStats} />
    </StatsDetailsLayout>
  );
};

export default PersonalStats;
