import { Box, Stack, Typography } from '@material-ui/core';

import { IProject } from '../../data/projects';
import React from 'react';
import StyledTypography from '../StyledTypography';

const Project = ({ title, technologies, description }: IProject) => {
  return (
    <>
      <Stack spacing={1}>
        <StyledTypography fontVariant="titleLight" capitalized={true}>
          {title}
        </StyledTypography>
        <Typography>{description}</Typography>
        <Box display="inline">
          <Typography
            variant="body2"
            display="inline"
            fontWeight="bold"
            color="text.disabled"
            component="span">
            HL Tech Stack:{' '}
          </Typography>
          <Typography variant="body2" display="inline">
            {technologies.join(', ')}
          </Typography>
        </Box>
      </Stack>
    </>
  );
};

export default Project;
