import React from 'react';
import ResumeDetailsLayout from './ResumeDetailsLayout';
import StyledNavLink from '../StyledNavLink';
import StyledTypography from '../StyledTypography';

const References = () => {
  return (
    <ResumeDetailsLayout
      title={
        <StyledNavLink to="/contact" label="Available upon request.">
          <StyledTypography
            bold={true}
            capitalized={true}
            variant="body1"
            textAlign="center"
            spaced={true}
            hoverEffect={true}>
            References available upon request
          </StyledTypography>
        </StyledNavLink>
      }>
      <></>
    </ResumeDetailsLayout>
  );
};

export default References;
