import React from 'react';
import ResumeDetailsLayout from './ResumeDetailsLayout';
import { Typography } from '@material-ui/core';

const Summary = () => {
  return (
    <>
      <ResumeDetailsLayout title="Summary">
        <Typography>
          Self-directed, insight-oriented, Technical Architect/Software Engineer
          and CSM with an excellent experience of 9+ years in e2e authoring
          solutions and leading teams towards complex and dynamic applications
          including early discovery, designing, developing, analyzing, and
          implementing in cloud technologies like Azure with modern stacks and
          best practices.
        </Typography>
        <Typography>
          Demonstrating strong critical thinking, decision making, problem
          solving, time management and multi-tasking.
        </Typography>
      </ResumeDetailsLayout>
    </>
  );
};

export default Summary;
