import degrees, { IDegree } from '../../data/resume/degree';

import Degree from './Education/Degree';
import React from 'react';
import ResumeDetailsLayout from './ResumeDetailsLayout';

const Education = () => {
  return (
    <ResumeDetailsLayout title="Education">
      {degrees.map((degree: IDegree) => (
        <Degree key={degree.degree} {...degree} />
      ))}
    </ResumeDetailsLayout>
  );
};

export default Education;
