import {
  IStats,
  siteStats as initialData,
  siteStatsApiUrl,
} from '../../data/stats/stats';
import React, { useCallback, useEffect, useState } from 'react';

import StatsDetailsLayout from './StatsDetailsLayout';
import StatsTable from './StatsTable';

const SiteStats = () => {
  const [siteStats, setSiteStats] = useState<IStats[]>(initialData);
  // TODO think about persisting this somewhere
  const fetchData = useCallback(async () => {
    // request must be authenticated if private
    const res = await fetch(siteStatsApiUrl, {
      cache: 'reload',
    });
    const resData = await res.json();
    setSiteStats(
      initialData.map((field) => ({
        ...field,
        value: Object.keys(resData).includes(field.key)
          ? resData[field.key]
          : field.value,
      })),
    );
  }, []);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  return (
    <StatsDetailsLayout title="Some stats about this site">
      <StatsTable stats={siteStats} />
    </StatsDetailsLayout>
  );
};

export default SiteStats;
