import { Box, Stack } from '@material-ui/core';

import Certifications from '../components/Resume/Certifications';
import Education from '../components/Resume/Education';
import Experience from '../components/Resume/Experience';
import PageContentLayout from '../layout/PageContentLayout';
import PageLayout from '../layout/PageLayout';
import React from 'react';
import { ReactNode } from 'react';
import References from '../components/Resume/References';
import Skills from '../components/Resume/Skills';
import StyledTypography from '../components/StyledTypography';
import Summary from '../components/Resume/Summary';
import useBreakpoints from '../hooks/useBreakpoints';

interface Section {
  title: string;
  details: ReactNode;
}
const sections: Section[] = [
  {
    title: 'Summary',
    details: <Summary />,
  },
  {
    title: 'Education',
    details: <Education />,
  },
  {
    title: 'Experience',
    details: <Experience />,
  },
  {
    title: 'Skills',
    details: <Skills />,
  },
  {
    title: 'Certifications',
    details: <Certifications />,
  },
  {
    title: 'References',
    details: <References />,
  },
];

const ResumeNavigation = () => {
  const { sm } = useBreakpoints();

  const scrollTo = (elementId: string) => {
    var timeoutRef = setTimeout(() => {
      document
        .getElementById(elementId.toLowerCase())
        ?.scrollIntoView({ behavior: 'smooth', block: 'start' });
      clearTimeout(timeoutRef);
    }, 100);
  };

  return (
    <Stack
      direction="row"
      justifyContent={sm ? 'center' : undefined}
      sx={{
        flexWrap: 'wrap',
      }}>
      {sections.map((section: Section) => (
        <StyledTypography
          key={section.title}
          bold={true}
          capitalized={true}
          hoverEffect={true}
          variant="overline"
          mr={2}
          onClick={() => {
            scrollTo(section.title);
          }}>
          {section.title}
        </StyledTypography>
      ))}
    </Stack>
  );
};

interface IResumeProps {}

const Resume = ({}: IResumeProps) => {
  const pageName = 'Resume';
  return (
    <>
      <PageLayout title={pageName} description={pageName}>
        <PageContentLayout header={pageName} subHeader={<ResumeNavigation />}>
          <Stack spacing={2}>
            {sections.map((section: Section) => (
              <Box
                key={section.title}
                display="flex"
                pb={2}
                id={section.title.toLowerCase()}>
                {section.details}
              </Box>
            ))}
          </Stack>
        </PageContentLayout>
      </PageLayout>
    </>
  );
};

export default Resume;
