import {
  Box,
  CircularProgress,
  Stack,
  Theme,
  Typography,
} from '@material-ui/core';
import { ICategory, ISkill } from '../../../data/resume/skills';

import React from 'react';

const CompetencyIndicator = ({
  value,
  color,
  label,
}: {
  value: number;
  color: string;
  label: string;
}) => {
  return (
    <Box sx={{ position: 'relative' }}>
      <CircularProgress
        value={value}
        variant="determinate"
        sx={{
          color: color,
        }}
      />
      <Box
        sx={{
          top: 0,
          left: 0,
          bottom: 0,
          right: 0,
          position: 'absolute',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}>
        <Typography variant="caption" component="div" color="text.secondary">
          {label || value}
        </Typography>
      </Box>
    </Box>
  );
};

interface ISkillBadgeProps {
  data: ISkill;
  categories: ICategory[];
}

const SkillBadge = ({ data, categories }: ISkillBadgeProps) => {
  const { categories: skillCategories, competency, title } = data;
  const skillColor = categories
    .filter((cat) => skillCategories.includes(cat.name))
    .map((cat) => cat.color)[0];

  return (
    <>
      <Box
        sx={{
          p: 1,
          border: (theme: Theme) => `1px solid ${theme.palette.grey[300]}`,
          borderRadius: 3,
          height: '100%',
        }}>
        <Stack alignItems="center" textAlign="center">
          <CompetencyIndicator
            value={(competency / 5.0) * 100.0}
            color={skillColor}
            label={`${competency}/5`}
          />
          <Typography variant="body2">{title}</Typography>
        </Stack>
      </Box>
    </>
  );
};

export default SkillBadge;
